const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  border: '2px solid #8DC53F',
  minWidth: 350
};

const PushAccount = () => {
  const { popAddAccount, setPopAddAccount, } = useContext(InfoAddAccount);
  const [multipleAcc, setMultipleAcc] = useState([])
  const [activeTab, setActiveTab] = useState(0)
  const [newAccAW, setNewAccAW] = useState(false);

  const handleChange_New_ACC = (event) => {
    setNewAccAW(event.target.checked);
  };
  const AddAccount = async (data) => {
    const options = {
      method: "POST",
      headers: {
        'Authorization': "Bearer " + localStorage.getItem('token'),
        "Content-type": "application/json",
        "Accept": "application/json",
      },
      body: JSON.stringify({
        account: data.account,
        email: data.email,
        password: data.password,
        secret: data.secret,
      }),
    };

    const SendPushTransaction = new Promise(async (resolve, rejected) => {
      await fetch("/accounts", options)
        .then(res => {
          if (res.status === 200) {
            resolve(true)
          } else {
            rejected(false)
          }
        })
        .catch(err => rejected(false));
    });

    toast.promise(
      SendPushTransaction,
      {
        pending: 'Promise is pending',
        success: {
          render({ data }) {
            return `ADD WAX : Succeed`
          },
        },
        error: {
          render({ data }) {
            // When the promise reject, data will contains the error
            return `ADD WAX : Failed`
          }
        }
      }, { theme: window.matchMedia("(prefers-color-scheme: dark)").matches ? "dark" : "light" }
    )
  };

  const AddAccountMultiple = async () => {
    for await (const multiple of multipleAcc) {
      const options = {
        method: "POST",
        headers: {
          'Authorization': "Bearer " + localStorage.getItem('token'),
          "Content-type": "application/json",
          "Accept": "application/json",
        },
        body: JSON.stringify({
          account: multiple.account,
          email: multiple.username,
          password: multiple.password,
          secret: multiple.secret,
        }),
      };

      const SendPushTransaction = new Promise(async (resolve, rejected) => {
        await fetch("/accounts", options)
          .then(res => {
            if (res.status === 200) {
              resolve(true)
            } else {
              rejected(false)
            }
          })
          .catch(err => rejected(false));
      });

      toast.promise(
        SendPushTransaction,
        {
          pending: 'Promise is pending',
          success: {
            render({ data }) {
              return `${multiple.account.toUpperCase()} : Succeed`
            },
          },
          error: {
            render({ data }) {
              // When the promise reject, data will contains the error
              return `${multiple.account.toUpperCase()} : Failed`
            }
          }
        }, { theme: window.matchMedia("(prefers-color-scheme: dark)").matches ? "dark" : "light" }
      )
    }
  }

  const [newAccount, setNewAccount] = useState({
    account: "",
    email: "",
    password: "",
    secret: "",
  });

  const onValueChange = (event) => {
    const { name, value } = event.target;
    setNewAccount((prevAccount) => {
      return {
        ...prevAccount,
        [name]: value,
      };
    });
  };

  const onValueChangeMultipleAcc = (event) => {
    const { value } = event.target;

    setMultipleAcc(JSON.parse(value));
  };

  return (<>
    <Modal
      open={popAddAccount}
      onClose={() => setPopAddAccount(!popAddAccount)}
      closeAfterTransition
      BackdropComponent={Backdrop}
      BackdropProps={{
        timeout: 500,
      }}
    >
      <Box sx={style} className={"text-gray-darker dark:text-gray-light bg-gray-lighter dark:bg-gray-darker rounded-xl font-semibold max-w-5xl mx-auto outline-none p-3"}>
        <div className="relative justify-center items-center">
          <div className="flex items-center justify-center">
            <div className="flex items-center">
              <div className="block whitespace-nowrap mb-4">
                <div className="flex flex-row uppercase">
                  <a className={`cursor-pointer flex w-full px-3 border-b-8 text-md font-medium ${activeTab === 0 ? "border-green text-green" : null}`} onClick={() => setActiveTab(0)} > Single </a>
                  <a className={`cursor-pointer flex w-full px-3 border-b-8 text-md font-medium ${activeTab === 1 ? "border-green text-green" : null}`} onClick={() => setActiveTab(1)} > Multiple </a>
                </div>
              </div>
            </div>
          </div>
          {activeTab === 0 ?
            <div className={`grid ${newAccAW === true ? "grid-cols-2 gap-4" : "grid-cols-1"}`}>
              <div className="flex flex-col space-y-2">
                <TextField
                  type='text'
                  variant="standard"
                  label="Account Wax"
                  name="account"
                  value={newAccount.account}
                  onChange={onValueChange}
                  size="small"
                />
                <TextField
                  type='email'
                  variant="standard"
                  label="E-Mail Wax"
                  name="email"
                  value={newAccount.email}
                  onChange={onValueChange}
                  size="small"
                />
                <TextField
                  type='password'
                  variant="standard"
                  label="Password Wax"
                  name="password"
                  value={newAccount.password}
                  onChange={onValueChange}
                  size="small"
                />
                <TextField
                  type='text'
                  variant="standard"
                  label="Secret Key"
                  name="secret"
                  value={newAccount.secret}
                  onChange={onValueChange}
                  size="small"
                />
                {/* <FormControlLabel
                  disabled
                  control={
                    <SwitchUnstyled
                      component={Root}
                      size="small"
                      checked={newAccAW}
                      onChange={handleChange_New_ACC}
                    />}
                  label="create a new account alienworlds.io"
                  labelPlacement="end"
                /> */}
              </div>
              <div className={`${newAccAW === true ? "block" : "hidden"}`}>
                <div className="flex flex-col space-y-2">
                  <TextField
                    type="number"
                    variant="standard"
                    label="Stack CPU"
                    name="s_cpu"
                    value={newAccount.password}
                    onChange={onValueChange}
                    size="small"
                  />
                  <TextField
                    type="number"
                    variant="standard"
                    label="Stack RAM"
                    name="s_ram"
                    value={newAccount.secret}
                    onChange={onValueChange}
                    size="small"
                  />
                  <TextField
                    type="number"
                    variant="standard"
                    label="Stack NET"
                    name="s_net"
                    value={newAccount.secret}
                    onChange={onValueChange}
                    size="small"
                  />
                </div>
              </div>
              <button className="flex w-full col-span-2 items-center mt-3 justify-center gap-1 font-bold outline-none uppercase tracking-wider focus:outline-none focus:shadow-none transition-all duration-300 rounded-lg py-1.5 px-6 text-xs leading-normal text-white bg-green hover:text-gray-darker focus:bg-green-dark active:bg-green-dark hover:shadow-inner"
                onClick={() => AddAccount(newAccount)}
              >
                Submit
              </button>
            </div> : <div className="flex flex-col">
              <TextareaAutosize
                className={`${multipleAcc.length === 0 ? "block" : "hidden"} text-xs`}
                onChange={onValueChangeMultipleAcc}
                maxRows={20}
                placeholder={`Input Json File`}
                defaultValue={JSON.stringify([{
                  "account": "xxxx.wam",
                  "secret": "XXXX XXXX XXXX XXXX",
                  "username": "xxxx@xxxx.com",
                  "password": "1234"
                },
                {
                  "account": "yyyy.wam",
                  "secret": "YYYY YYYY YYYY YYYY",
                  "username": "yyyy@yyyy.com",
                  "password": "1234"
                },
                {
                  "account": "zzzz.wam",
                  "secret": "ZZZZ ZZZZ ZZZZ ZZZZ",
                  "username": "zzzz@zzzz.com",
                  "password": "1234"
                },], null, 4)}
                style={{ width: 450, padding: 5, marginBottom: 10 }}
              />
              <div className="text-xs">
                Multiple Add : {multipleAcc.length} Account
                <div className="flex flex-col max-h-52 mb-2 overflow-y-scroll">
                  {multipleAcc.map((i) => {
                    return (<li key={i.account}>Account : {i.account}</li>)
                  })}
                </div>
              </div>
              <button className="flex w-full items-center justify-center gap-1 font-bold outline-none uppercase tracking-wider focus:outline-none focus:shadow-none transition-all duration-300 rounded-lg py-1.5 px-6 text-xs leading-normal text-white bg-green hover:text-gray-darker focus:bg-green-dark active:bg-green-dark hover:shadow-inner"
                onClick={() => AddAccountMultiple(newAccount)}
              >
                Submit
              </button>
            </div>}
        </div>
      </Box>
    </Modal>
  </>);
}

export default PushAccount;

