    //                         })
    //                         clearInterval(trxCheck);
    //                     }
    //                 })
    //                 .catch(err => console.error('error:' + err));
    //         }, 5000)
    //     }
    // }

    // const CheckTrxConfirmations = () => {
    //     const i = coinexTrx.data.confirmations / 360 * 100
    //     if (i >= 100) {
    //         return (100)
    //     }
    //     return i
    // }

    // const SetAddMasterKey = async (Key) => {
    //     const SendPushTransaction = new Promise(async (resolve, rejected) => {
    //         await fetch('https://alien-script.com/v1/api/member', {
    //             method: 'POST',
    //             headers: { 'Content-Type': 'application/json' },
    //             body: JSON.stringify({ master_key: Key })
    //         })
    //             .then(res => res.json())
    //             .then(json => {
    //                 if (json.code === "error") {
    //                     rejected(json.data.message)
    //                 } else {
    //                     resolve(json)
    //                 }
    //             })
    //             .catch(err => rejected(err));
