    const GetHistory24Hr = async () => {
        await fetch("https://wax.alcor.exchange/api/pools/0/charts?period=24H")
            .then((res) => res.json())
            .then((json) => {
                console.log(json);
                let labels = [];
                let dataTLM = [];
                json.forEach((element) => {
                    labels.push(dayjs.utc(element._id).local().format("HH:mm"));
                    dataTLM.push(element.price);
                });
                setData24Hr({
                    labels: labels,
                    datasets: [
                        {
                            label: "LTM / WAX ",
                            data: dataTLM,
                            fill: true,
                            backgroundColor: "rgba(141, 197, 63,0.2)",
                            borderColor: "rgba(141, 197, 63,1)"
                        }
                    ]
                });
            })
            .catch((err) => console.error("error:" + err));
    };

    const getPriceSwap = async () => {
        await fetch("https://wax.greymass.com/v1/chain/get_table_rows", {
            "headers": {
                "accept": "*/*",
                "accept-language": "en-US,en;q=0.9",
                "content-type": "text/plain;charset=UTF-8",
                "sec-fetch-dest": "empty",
                "sec-fetch-mode": "cors",
                "sec-fetch-site": "cross-site",
                "sec-gpc": "1"
            },
            "referrer": "https://wax.alcor.exchange/",
            "referrerPolicy": "strict-origin-when-cross-origin",
            "body": JSON.stringify({ "json": true, "code": "alcorammswap", "scope": "alcorammswap", "table": "pairs", "table_key": "", "lower_bound": 0, "upper_bound": 0, "index_position": 1, "key_type": "", "limit": 1, "reverse": false, "show_payer": false }),
            "method": "POST",
            "mode": "cors"
        }).then(res => res.json())
            .then(json => {
                setSwapPrice({
                    tlm: (
                        parseFloat(json.rows[0].pool1.quantity) /
                        parseFloat(json.rows[0].pool2.quantity)
                    ).toFixed(8),
                    wax: (
                        parseFloat(json.rows[0].pool2.quantity) /
                        parseFloat(json.rows[0].pool1.quantity)
                    ).toFixed(8),
                });
            })
            .catch(err => console.error('error:' + err));
    }

    const PushSetTools = async () => {
        const Tools = []
        if (bagValue.bag_1 !== "") {
            Tools.push(bagValue.bag_1)
        }
        if (bagValue.bag_2 !== "") {
            Tools.push(bagValue.bag_2)
        }
        if (bagValue.bag_3 !== "") {
            Tools.push(bagValue.bag_3)
        }

        const options = {
            method: 'POST',
            headers: {
                'Authorization': "Bearer " + localStorage.getItem('token'),
                "Content-type": "application/json",
                "Accept": "application/json",
            },
            body: JSON.stringify({
                account: pushTrx.account,
                actions: [
                    {
                        account: "m.federation",
                        name: "setbag",
                        authorization: [
                            {
                                actor: pushTrx.account,
                                permission: "active",
                            },
                        ],
                        data: {
                            account: pushTrx.account,
                            items: Tools.slice(0, 3),
                        },
                    },
                ]

            })
        };


        const SendPushTransaction = new Promise(async (resolve, rejected) => {
            await fetch("/push_transaction", options)
                .then(res => res.json())
                .then(json => {
                    console.log(json);
                    if (json.transaction_id) {
                        resolve(json)
                    } else {
                        setLogin(false)

                        rejected(false)
                    }
                })
                .catch(err => rejected(err));
        });

        toast.promise(
            SendPushTransaction,
            {
                pending: 'Promise is pending',
                success: {
                    render({ data }) {
                        return `Succeed : ${data.transaction_id.slice(0, 10)}`
                    },
                },
                error: {
                    render({ data }) {
                        // When the promise reject, data will contains the error
                        return `Failed`
                    }
                }
            }, { theme: window.matchMedia("(prefers-color-scheme: dark)").matches ? "dark" : "light" }
        )
    }

    const PushSetStake = async () => {
        const actions = []
        if (contextMemo.type === "stake") {
            switch (contextMemo.typeStake) {
                case "cpu":
                    actions.push(
                        {
                            account: 'eosio',
                            name: "delegatebw",
                            authorization: [
                                {
                                    "actor": pushTrx.account,
                                    "permission": "active"
                                },
                            ],
                            data: {
                                from: pushTrx.account,
                                receiver: pushTrx.account,
                                stake_net_quantity: "0.00000000 WAX",
                                stake_cpu_quantity: `${parseFloat(contextMemo.quantity).toFixed(8)} WAX`,
                                transfer: false
                            },
                        },
                    )
                    break;
                case "net":
                    actions.push(
                        {
                            account: 'eosio',
                            name: "delegatebw",
                            authorization: [
                                {
                                    "actor": pushTrx.account,
                                    "permission": "active"
                                },
                            ],
                            data: {
                                from: pushTrx.account,
                                receiver: pushTrx.account,
                                stake_net_quantity: `${parseFloat(contextMemo.quantity).toFixed(8)} WAX`,
                                stake_cpu_quantity: "0.00000000 WAX",
                                transfer: false
                            },
                        },
                    )
                    break;
                case "ram":
                    actions.push(
                        {
                            account: 'eosio',
                            name: "buyram",
                            authorization: [
                                {
                                    "actor": pushTrx.account,
                                    "permission": "active"
                                },
                            ],
                            data: {
                                "payer": pushTrx.account,
                                "quant": `${parseFloat(contextMemo.quantity).toFixed(8)} WAX`,
                                "receiver": pushTrx.account,
                                transfer: false
                            },
                        },
                    )
                    break;

                default:
                    break;
            }
        } else {
            switch (contextMemo.typeStake) {
                case "cpu":
                    actions.push(
                        {
                            account: 'eosio',
                            name: "undelegatebw",
                            authorization: [
                                {
                                    "actor": pushTrx.account,
                                    "permission": "active"
                                },
                            ],
                            data: {
                                from: pushTrx.account,
                                receiver: pushTrx.account,
                                unstake_net_quantity: "0.00000000 WAX",
                                unstake_cpu_quantity: `${parseFloat(contextMemo.quantity).toFixed(8)} WAX`,
                                transfer: false
                            },
                        },
                    )
                    break;
                case "net":
                    actions.push(
                        {
                            account: 'eosio',
                            name: "undelegatebw",
                            authorization: [
                                {
                                    "actor": pushTrx.account,
                                    "permission": "active"
                                },
                            ],
                            data: {
                                from: pushTrx.account,
                                receiver: pushTrx.account,
                                unstake_net_quantity: `${parseFloat(contextMemo.quantity).toFixed(8)} WAX`,
                                unstake_cpu_quantity: "0.00000000 WAX",
                                transfer: false
                            },
                        },
                    )
                    break;
                case "ram":
                    actions.push(
                        {
                            account: 'eosio',
                            name: "sellram",
                            authorization: [
                                {
                                    "actor": pushTrx.account,
                                    "permission": "active"
                                },
                            ],
                            data: {
                                account: pushTrx.account,
                                bytes: parseInt(contextMemo.quantity),
                                transfer: false
                            },
                        },
                    )
                    break;

                default:
                    break;
            }
        }


        const options = {
            method: 'POST',
            headers: {
                'Authorization': "Bearer " + localStorage.getItem('token'),
                "Content-type": "application/json",
                "Accept": "application/json",
            },
            body: JSON.stringify({
                account: pushTrx.account,
                actions: actions
            })
        };


        const SendPushTransaction = new Promise(async (resolve, rejected) => {
            await fetch("/push_transaction", options)
                .then(res => res.json())
                .then(json => {
                    console.log(json);
                    if (json.transaction_id) {
                        resolve(json)
                    } else {
                        setLogin(false)
                        rejected(false)
                    }
                })
                .catch(err => rejected(err));
        });

        toast.promise(
            SendPushTransaction,
            {
                pending: 'Promise is pending',
                success: {
                    render({ data }) {
                        return `Succeed : ${data.transaction_id.slice(0, 10)}`
                    },
                },
                error: {
                    render({ data }) {
                        // When the promise reject, data will contains the error
                        return `Failed`
                    }
                }
            }, { theme: window.matchMedia("(prefers-color-scheme: dark)").matches ? "dark" : "light" }
        )
    }

