/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable react/jsx-no-target-blank */
import React, { useState, useEffect, useContext } from "react";
import preval from 'preval.macro';
import { ReBootServer } from "../components/models/Reboot";
import socketIOClient from "socket.io-client";
import TableProfile from "../components/content/TableProfile";
import Tooltip from '@mui/material/Tooltip';
import { MainProFileContext } from "../App";
const fetch = require("node-fetch");


export default function MainProfile() {
    const { popAddAccount, setPopAddAccount, popMemberShipAW, setPopMemberShipAW, masterKey, setMasterKey, setResponse, totalTlm, totalWax, TLMUSDT, setTLMUSDT, WAXPUSDT, setWAXPUSDT, setLogin } = useContext(MainProFileContext);
    const [hostCPU, setHostCPU] = useState(0)
    const version = preval`
                            const { execSync } = require('child_process')
                            const rawLog = execSync('git log --no-decorate -n 1 --no-merges', {
                            encoding: 'utf-8',
                            })
                            module.exports = /commit ([a-z0-9]+)/gi.exec(rawLog)[1]
                            `
    const Get_WAXPUSDT = async () => {
        await fetch("https://api.binance.com/api/v3/ticker/price?symbol=WAXPUSDT")
            .then(res => res.json())
            .then(json => { setWAXPUSDT(json.price) })
            .catch(err => console.error('error:' + err));
    }

    const Get_TLMUSDT = async () => {
        await fetch("https://api.binance.com/api/v3/ticker/price?symbol=TLMUSDT")
            .then(res => res.json())
            .then(json => { setTLMUSDT(json.price) })
            .catch(err => console.error('error:' + err));
    }

    useEffect(() => {

        if (window.location.href.includes("localhost")) {
            const data = require("../components/dataFack.json");
            setMasterKey({
                master_key: "8WA51C6CBA",
                coin: 0
            })
            setHostCPU({ percentageCPU: 95, percentageMem: 85, ipServer: "123.123.123.123" })
            setResponse(data);
            setLogin(false)
        } else {
            const socket = socketIOClient(window.location.href);
            socket.on("MasterKey", (data) => { setMasterKey(data) });
            socket.on("FromAPI", (data) => { setResponse(data); });
            socket.on("HostResources", (data) => { setHostCPU(data); });
        }
        Get_WAXPUSDT()
        Get_TLMUSDT()
        setInterval(async () => { await Get_WAXPUSDT(); await Get_TLMUSDT(); }, 60000);
    }, []);

    return (
        <div>
            <section className="relative min-h-screen py-3  bg-gray-lighter text-black dark:bg-gray-darkest dark:text-white">
                <div className="container mx-auto px-4">
                    <div className="relative flex items-end justify-end uppercase" style={{ fontSize: "14px" }}>
                        Version : {version.substr(0, 7)}
                    </div>
                    <div className="relative flex flex-col min-w-0 break-words  bg-gray-lighter text-black dark:bg-gray-darkest dark:text-white w-full mb-6 shadow-xl rounded-lg mt-4">
                        <div className="p-6 text-right ">
                            <div className="flex flex-wrap justify-center">
                                <div className="w-full flex justify-center">
                                    <div className="relative ">
                                        <img
                                            alt="logo"
                                            src={
                                                require("../assets/img/logo-new.png").default
                                            }
                                            className="h-auto "
                                            style={{ maxWidth: "300px" }}
                                        />
                                    </div>
                                </div>
                                <div className="flex w-full flex-col justify-items-end sm:flex-row sm:mt-6 sm:space-x-3.5 sm:justify-center sm:items-center">
                                    <div className="flex flex-nowrap items-center space-x-2">
                                        <img
                                            className="h-4 sm:h-10"
                                            src={require("../assets/img/tlm.png").default}
                                            alt="TLM"
                                        />
                                        <div className="sm:flex sm:w-full sm:flex-col sm:justify-start">
                                            <strong className="whitespace-nowrap flex-grow sm:flex-grow-0 text-sm sm:text-xl text-gray-darker dark:text-gray-light">{Number.parseFloat(totalTlm).toFixed(4)} TLM</strong>
                                            <span style={{ fontSize: 8 }} className="invisible sm:visible -mt-4 whitespace-nowrap flex-grow sm:flex-grow-0 text-sm sm:text-xl text-gray-darker dark:text-gray-light font-semibold">1 TLM : {Number.parseFloat(TLMUSDT).toFixed(3)} USDT</span>
                                        </div>
                                    </div>
                                    <div className="flex items-center space-x-2">
                                        <img
                                            className="h-4 sm:h-10"
                                            src={require("../assets/img/wax.png").default}
                                            alt="WAX"
                                        />
                                        <div className="sm:flex sm:w-full sm:flex-col sm:justify-start">
                                            <strong className="whitespace-nowrap flex-grow sm:flex-grow-0 text-sm sm:text-xl text-gray-darker dark:text-gray-light">{Number.parseFloat(totalWax).toFixed(8)} WAX</strong>
                                            <span style={{ fontSize: 8 }} className="invisible sm:visible -mt-4 whitespace-nowrap flex-grow sm:flex-grow-0 text-sm sm:text-xl text-gray-darker dark:text-gray-light font-semibold">1 WAX : {Number.parseFloat(WAXPUSDT).toFixed(3)} USDT</span>
                                        </div>
                                    </div>
                                    <div className="flex flex-nowrap items-center space-x-2">
                                        <img
                                            className="h-4 sm:h-10"
                                            src={require("../assets/img/usdt.png").default}
                                            alt="USDT"
                                        />
                                        <div className="sm:flex sm:w-full sm:flex-col sm:justify-start">
                                            <strong className="whitespace-nowrap flex-grow sm:flex-grow-0 text-sm sm:text-xl text-gray-darker dark:text-gray-light">{Number((Number.parseFloat(totalTlm) * Number.parseFloat(TLMUSDT)) + (Number.parseFloat(totalWax) * Number.parseFloat(WAXPUSDT))).toFixed(3)} USDT</strong>
                                        </div>

                                    </div>
                                    <div className="flex flex-nowrap items-center space-x-2">
                                        <img
                                            className="h-4 sm:h-10"
                                            src={require("../assets/img/logo.png").default}
                                            alt="USDT"
                                        />
                                        <div className="sm:flex sm:w-full sm:flex-col sm:justify-start">
                                            <strong className="whitespace-nowrap flex-grow sm:flex-grow-0 text-sm sm:text-xl text-gray-darker dark:text-gray-light">{Number(masterKey.coin).toFixed(4)} COIN</strong>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div>
                        <div className="grid grid-cols-none md:grid-cols-2 items-center align-middle">
                            <div className={"flex justify-center md:justify-start space-x-2"}>
                                <button
                                    className={`text-purple-500 border-2 border-solid rounded-xl border-purple-500 hover:bg-purple-500 hover:text-white active:bg-purple-600 font-bold uppercase text-xs px-4 py-2 outline-none focus:outline-none mb-1 ease-linear transition-all duration-150`}
                                    type="button"
                                    onClick={() => setPopMemberShipAW(!popMemberShipAW)}
                                >
                                    Member Ship
                                </button>
                            </div>
                            <div className={"flex justify-center md:justify-end space-x-2"}>
                                <button
                                    className={`text-purple-500 border-2 border-solid rounded-xl border-purple-500 hover:bg-purple-500 hover:text-white active:bg-purple-600 font-bold uppercase text-xs px-4 py-2 outline-none focus:outline-none mb-1 ease-linear transition-all duration-150`}
                                    type="button"
                                    onClick={async () => {
                                        await fetch("/start", { method: "GET", headers: { 'Authorization': "Bearer " + localStorage.getItem('token'), }, });
                                    }}
                                >
                                    Start All
                                </button>
                                <button
                                    className={`text-purple-500 border-2 border-solid rounded-xl border-purple-500 hover:bg-purple-500 hover:text-white active:bg-purple-600 font-bold uppercase text-xs px-4 py-2 outline-none focus:outline-none mb-1 ease-linear transition-all duration-150`}
                                    type="button"
                                    onClick={async () => {
                                        await fetch("/stop", { method: "GET", headers: { 'Authorization': "Bearer " + localStorage.getItem('token'), }, });
                                    }}
                                >
                                    Stop All
                                </button>
                                <button
                                    className={`text-purple-500 border-2 border-solid rounded-xl border-purple-500 hover:bg-purple-500 hover:text-white active:bg-purple-600 font-bold uppercase text-xs px-4 py-2 outline-none focus:outline-none mb-1 ease-linear transition-all duration-150`}
                                    type="button"
                                    onClick={() => setPopAddAccount(!popAddAccount)}
                                >
                                    + Account
                                </button>

                            </div>
                        </div>
                        <div className="block mt-3">
                            <TableProfile />
                        </div>
                        <div className="flex items-center justify-center">
                            <div className="flex flex-col">
                                <div className="flex flex-row w-80 space-x-2">
                                    <div className="w-full bg-gray-light rounded-md h-3 mb-1.5 whitespace-nowrap">
                                        <div
                                            className={`${hostCPU.percentageCPU > 75 ? hostCPU.percentageCPU > 90 ? "bg-danger" : "bg-warning" : "bg-green"} rounded-md text-center h-3`}
                                            style={{ width: `${hostCPU.percentageCPU}%`, maxWidth: "100%", fontSize: "8px" }}
                                        >
                                            CPU {hostCPU.percentageCPU}%
                                        </div>
                                    </div>
                                    <div className="w-full bg-gray-light rounded-md h-3 whitespace-nowrap">
                                        <div
                                            className={`${hostCPU.percentageMem > 75 ? hostCPU.percentageMem > 90 ? "bg-danger" : "bg-warning" : "bg-green"} rounded-md text-center h-3`}
                                            style={{ width: `${hostCPU.percentageMem}%`, maxWidth: "100%", fontSize: "8px" }}
                                        >
                                            RAM {hostCPU.percentageMem}%
                                        </div>
                                    </div>
                                </div>
                                <Tooltip title="Re-Boot Server">
                                    <div className="text-center cursor-pointer" onClick={() => ReBootServer()} style={{ fontSize: "10px" }}>
                                        IP : {hostCPU.ipServer}
                                    </div>
                                </Tooltip>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    )
}